import { ComponentProps } from 'react';
import dynamic from 'next/dynamic';

import { Button as SpButton } from '.';
import { ClearButton as SpClearButton } from '.';
import { CloseButton as SpCloseButton } from '.';

export const Button = dynamic<
    JSX.LibraryManagedAttributes<
        typeof SpButton,
        ComponentProps<typeof SpButton>
    >
>(() => import('.').then(({ Button }) => Button), { ssr: false });
export const ClearButton = dynamic<
    JSX.LibraryManagedAttributes<
        typeof SpClearButton,
        ComponentProps<typeof SpClearButton>
    >
>(() => import('.').then(({ ClearButton }) => ClearButton), { ssr: false });
export const CloseButton = dynamic<
    JSX.LibraryManagedAttributes<
        typeof SpCloseButton,
        ComponentProps<typeof SpCloseButton>
    >
>(() => import('.').then(({ CloseButton }) => CloseButton), { ssr: false });
